.minimizable-web-chat>button.maximize {
  background-color: #667CB1;
  border-radius: 50%;
  border: 2px solid #667CB1;
  bottom: 20px;
  box-shadow: 0 0 20px rgba(0, 0, 0, .2);
  color: White;
  font-size: 30px;
  height: 64px;
  outline: 0;
  padding: 15px;
  position: fixed;
  right: 20px;
  width: 64px;
}

.minimizable-web-chat>button.maximize>.red-dot {
  color: #375497;
  font-size: 14px;
  position: absolute;
  right: 12px;
  top: 12px;
}

.minimizable-web-chat>button.maximize:focus:not(:active), .minimizable-web-chat>button.maximize:hover:not(:active) {
  background-color: White;
  border-color: #112D70;
  color: #112D70
}

.minimizable-web-chat>button.maximize:active {
  background-color: White;
  border-color: #112D70;
  color: #112D70
}

